body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.navFlex{
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

img{
    height: 100%;
    width: auto;
    margin-left: 50px;
}

.navButton{
    background: none;
    border: none;
    border-radius: 5px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 1.2em;
    color: rgba(66, 116, 244);
    transition: background-color 0.5s ease;
    margin: 10px 50px 10px 0px;
    cursor: pointer;
}

.navButton:hover{
    background-color: rgba(0,0,0,0.2);
    box-shadow: 5px 5px rgba(0,0,0,0.05);
}

.navButton:active{
    transition: none;
    background-color: rgba(0,0,0,0.8);
}
.bgVideo{
    width: 100%;
    height: auto;
    z-index: -1;
    position: relative;
}

.videoBox{
    position: relative;
    border-top: 2px solid black;
    height: 750px;
    overflow: hidden;
}
.messageBox{
    position: absolute;
    top: 0;
    width: 100%;
    color: whitesmoke;
    margin-top: 200px;
    text-shadow: 2px 2px rgba(0,0,0,0.2);
}

.lion{
    margin: 0;
    left: -240px;
    height: 650px;
    position: relative;
}

.messageBox hr{
    width: 20%;
    background-color: whitesmoke;
}

@media only screen and (max-width: 1250px){
    .bgVideo{
        height: 100%;
        width: auto;
    }

    .videoBox{
        height: 600px;
    }
}
footer{
    height: 90px;
    font-size: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-top: 2px solid black;
}

.socialFlex{
    display: -webkit-flex;
    display: flex;
    width: 300px;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

a{
    text-decoration: none;
    color: black;
}

a:hover{
    text-decoration: none;
}

a:active{
    text-decoration: none;
}

@media only screen and (max-width: 1200px){
    footer{
        height: 120px;
    }
}
.aboutP{
    margin-top: 10;
    overflow: hidden;
    padding: 0 10px;
}

Transition{
    margin: 0;
}

.containerT{
    overflow: hidden;
    border-top: 2px solid black;
}
